@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@900&display=swap");
/*NEW FONT ROBOTO*/
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

body {
  background-color: var(--lighter-gray);
  font-family: "Roboto", sans-serif;
}

.App .container {
  width: 100%;
  height: 100vh;
  background-image: url('../images/pexels-apasaric.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.login-form {
  width: 450px;
  margin: auto;
  text-align: center;
  background-color: var(--light-blue);
  /* background: linear-gradient(to bottom, rgba(4, 217, 217, 0.4), rgba(136, 223, 242, 0.4)); */
  border-radius: 10px;
  color: var(--ocean-blue);
  font-size: 26px;
  padding: 25px;
}

/* TTILE PAGE ANIMATION STYLES */

.login-form section {
  /* display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; */
  min-height: 10vh;
  font-size: 2em;
  background: transparent;
  color: var(--sea-blue);
  text-transform: uppercase;
  gap: 30px;
}

.login-form section h1 {
  font-size: 1em;
  text-decoration: underline;
  @media (max-width: 768px) {
    font-size: 1.3em;
  }
}

.login-form section .text-shadow {
  font-style: italic;
  text-transform: uppercase;
  color: transparent;
  -webkit-text-stroke: 1px var(--white);
  -webkit-text-stroke-width: 1px;
  text-shadow: 2px 2px 10px var(--sea-blue);
  transition: all 0.5s ease-in-out;
  text-align: center;
  letter-spacing: 0.2em;
  animation: flicker 0.5s ease-in-out infinite alternate;

  &:hover {
    color: var(--white);
  }
}

@keyframes flicker {
	0% {
		opacity: 0.5;
		text-shadow: 2px 2px 10px var(--sea-blue);
	}
	100% {
		opacity: 1;
		text-shadow: 2px 2px 20px var(--sea-blue);
	}
}

/* TTILE PAGE ANIMATION STYLES END */

/* LOADING SVG STYLES */

#svg-spinner{
  -webkit-transition-property: -webkit-transform;
  -webkit-transition-duration: 1.2s;
  -webkit-animation-name: rotate;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;

  -moz-transition-property: -moz-transform;
  -moz-animation-name: rotate;
  -moz-animation-duration: 1.2s;
  -moz-animation-iteration-count: infinite;
  -moz-animation-timing-function: linear;

  transition-property: transform;
  animation-name: rotate;
  animation-duration: 1.2s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@-webkit-keyframes rotate {
  from {-webkit-transform: rotate(0deg);}
  to {-webkit-transform: rotate(360deg);}
}

@-moz-keyframes rotate {
  from {-moz-transform: rotate(0deg);}
  to {-moz-transform: rotate(360deg);}
}

@keyframes rotate {
  from {transform: rotate(0deg);}
  to {transform: rotate(360deg);}
}

/* SPINNER POSITION */

#svg-spinner{
  position: absolute;
  top: 40%;
  left: 50%;
}

#svg-para{
  position: absolute;
  top: 40%;
  width: 40%;
  padding: 5%;
  text-align: center;
  left: 33%;
  color: var(--beach-blue);
  font-size: 2.5em;
}

/* LOADING SVG STYLES END */

.login-form img {
  border-radius: 50%;
  border: 1px solid var(--dark-gray);
}

.login-form h2 {
  text-transform: uppercase;
  color: var(--white);
  font-size: 30px;
}

.login-form form {
  margin: 25px auto 0 auto;
  width: 80%;
  display: flex;
  flex-direction: column;
}

.login-form form .input-container {
  position: relative;
  margin-bottom: 10px;
}

.login-form form .input-container .input-icon {
  position: absolute;
  top: 35%;
  left: 15px;
  transform: translateY(-50%);
  transition: all 0.3 ease-in-out;
}

.login-form form .input-container .input-icon img {
  width: 20px;
  height: 20px;
}

.login-form form input[type='text'],
.login-form form input[type='password'] {
  margin-bottom: 10px;
  padding: 10px;
  font-size: 18px;
  font-family: "Roboto", sans-serif;
  /* background-color: rgba(4, 178, 217, 0.3); */
  background: transparent;
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.25);
  color: #fff;
  transition: border-bottom-color 0.3s ease-in-out;
}

/* .login-form form input[type='text']:focus + .input-icon,
.login-form form input[type='password']:focus + .input-icon {
  transform: translateY(-120%) scale(1);
} */

.login-form form input[type='text']:focus,
.login-form form input[type='password']:focus {
  outline: none;
  border-bottom: 1px solid rgba(255, 255, 255, 1);
  animation: borderColorTransition 0.3s ease-in-out forwards;
}

.login-form form input[type='text']:focus::placeholder,
.login-form form input[type='password']:focus::placeholder {
  color: transparent;
}

.login-form form input[type='text']::placeholder,
.login-form form input[type='password']::placeholder {
  color: var(--white);
  padding-left: 20px;
}

@keyframes borderColorTransition {
  0% {
    border-bottom-color: 1px solid rgba(255, 255, 255, 0.3);
  }
  100% {
    border-bottom-color: 1px solid rgba(255, 255, 255, 1);
  }
}

.login-form form .input-container .pwd-toggle {
  position: absolute;
  right: 50px;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  cursor: pointer;
}

.login-form form input[type='submit'] {
  background-color: var(--white);
  border: none;
  width: 100px;
  height: 40px;
  border-radius: 15px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  color: var(--beach-blue);
  font-family: "Roboto", sans-serif;
  margin: 15px auto 0 auto;
  transition: all 0.3s ease-in-out;
}

.login-form form input[type='submit']:hover {
  /* border: 1px solid var(--beach-blue); */
  background: linear-gradient(to bottom, var(--beach-blue), var(--sea-blue));
  color: var(--white);
}

#error-msg {
  color: #ff295b;
  font-size: 16px;
  font-family: "Ga Maamli", sans-serif;
  text-align: center;
}

/*

PROFILE PAGE STYLES

*/

.App marquee {
  color: var(--ocean-blue);
  font-size: 18px;
  text-transform: capitalize;
}

.profile-page {
  display: flex;
  flex-direction: row;
  width: 100%;
  padding: 10px;
  justify-content: space-between;
}

.profile-page .profile-header {
  display: flex;
  flex-direction: column;
  width: 350px;
  background-color: var(--white);
  border-radius: 15px;
  padding: 5px;
}

.profile-page .profile-header .row {
  font-family: 'Roboto', sans-serif;;
}

.profile-page .profile-header .row h1 {
  color: var(--ocean-blue);
  text-align: left;
  font-size: 24px;
}

.profile-page .profile-header .row h3 {
  color: var(--dark-gray);
}

.profile-page .profile-header .row h3 img {
  margin-left: 10px;
}

.profile-page .profile-header .row h3 span {
  margin-left: 15px;
  font-size: 12px;
  text-transform: uppercase;
  font-weight: bolder;
  padding: 5px;
}

.profile-page .profile-header .row h3 .admin {
  background-color: var(--mantis);
  color: var(--delft-blue);
}

.profile-page .profile-header .row h3 .follower {
  background-color: var(--red-lightish);
  color: var(--white);
}

.profile-page .profile-header .row ul {
  list-style: none;
  margin-top: 20px;
  padding: 5px;
  font-size: 16px;
  width: 100%;
}

.profile-page .profile-header .row ul li {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
}

.profile-page .profile-header .row ul li span {
  text-align: right;
}

.profile-page .profile-header .row button {
  width: 150px;
  height: 45px;
  background: transparent;
  border: none;
  color: var(--dark-gray);
  font-size: 20px;
  font-family: "Roboto", sans-serif;
  cursor: pointer;
  padding: 5px;
  /* box-shadow: 2px 2px 10px var(--delft-blue); */
}

.profile-page .profile-header .row button .logout-icon {
  margin-top: 10px;
}

.profile-page .profile-header .row button .logout-icon img {
  width: 24px;
  height: 24px;
}

.profile-page .profile-header .row button .logout-text {
  margin-left: 10px;
  position: relative;
  bottom: 5px;
}

.profile-page .profile-header .row button:hover {
  /* border: 1px solid var(--white); */
}

/* START OF PROFILE BODY STYLES */
.profile-page .profile-body {
  /* background-color: var(--white); */
  padding: 10px;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  margin-left: 15px;
  width: 100%;
}

.profile-page .profile-body h2 {
  font-size: 38px;
}

.profile-page .profile-body .row {
  display: flex;
  flex-direction: row;
  /* justify-content: space-between; */
}

.profile-page .profile-body .row:last-child {
  margin-top: 50px;
}

.profile-page .profile-body .row .col {
  background-color: var(--white);
  margin: 15px;
  padding: 15px;
  border-radius: 10px;
}

.profile-page .profile-body .row .col .overview {
  padding: 5px;
}

.profile-page .profile-body .row .col .overview .audit-shape {
  display: flex;
  flex-direction: column;
}

.profile-page .profile-body .row .col .overview .audit-shape .audit-circle {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

.profile-page .profile-body .row .col .overview .audit-shape .audit-circle p {
  color: var(--white);
  margin: 33% auto;
  width: fit-content;
  font-size: 28px;
}

.profile-page .profile-body .row .col .overview .audit-footer {
  margin-top: 20px;
  font-size: 26px;
  stroke: 1px var(--mantis);
  color: var(--delft-blue);
  font-weight: bolder;
}

.profile-page .profile-body .row .col .overview .audit-footer p {
  text-align: justify;
}

.profile-page .profile-body .row .col .overview .level-data-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
}

.profile-page .profile-body .row .col .overview .level-data-container .level-data {
  background-color: var(--ocean-blue);
  width: 220px;
  height: 100px;
  padding: 5px;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.profile-page .profile-body .row .col .overview .level-data-container .level-data p {
  color: var(--white);
  font-size: 52px;
  font-weight: 900;
  text-align: center;
}

.profile-page .profile-body .row .col .overview .xp-data-container {
  margin-top: 25px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.profile-page .profile-body .row .col .overview .xp-data-container .xp-data {
  background-color: var(--sea-blue);
  width: 220px;
  height: 70px;
  padding: 5px;
  border-radius: 10px;
}

.profile-page .profile-body .row .col .overview .xp-data-container p {
  color: var(--white);
  font-size: 48px;
  font-weight: bold;
  text-align: center;
}

.profile-page .profile-body .row .col .audits-container figure .chart .bar {
  height: 21px;
  transition: fill .3s ease;
  cursor: pointer;
  font-family: 'Roboto', sans-serif;

  text {
    fill: var(--dark-gray);
  }
}

.profile-page .profile-body .row .col .audits-container figure .chart .passed {
  fill: var(--ocean-blue);
}

.profile-page .profile-body .row .col .audits-container figure .chart .failed {
  fill: var(--red-lightish);
}

.profile-page .profile-body .row .col .audits-container figure .chart .escaped {
  fill: var(--english-violet);
}

.profile-page .profile-body .row .col .audits-container figure .chart:hover,
.profile-page .profile-body .row .col .audits-container figure .chart:focus {
  .passed {
    fill: var(--light-blue);
  }
}

.profile-page .profile-body .row .col .audits-container figure .chart:hover,
.profile-page .profile-body .row .col .audits-container figure .chart:focus {
  .failed {
    fill: var(--red-lighter);
  }
}

.profile-page .profile-body .row .col .audits-container figure .chart:hover,
.profile-page .profile-body .row .col .audits-container figure .chart:focus {
  .escaped {
    fill: var(--english-lighter);
  }
}

#my-skills {
  width: 100%;
  max-width: 300px;
}

#my-skills .bar {
}

#my-skills h4 {
  text-align: right;
  margin-top: 20px;
  font-size: 18px;
}

#my-skills table tbody tr .custom-bar-color {
  --color: var(--ocean-blue);
  cursor: pointer;
  transition: all .2s ease;
}

#my-skills table tbody tr .custom-bar-color:hover {
  --color: var(--light-blue);
}

#my-skills .legend {
  /**/
}

.profile-page .profile-extra {
  display: flex;
  flex-direction: column;
  width: 350px;
  background-color: var(--white);
  padding: 10px;
  font-family: "Roboto", sans-serif;
  margin-left: 15px;
  border-radius: 15px;
}

.profile-page .profile-extra .row .col1 .pending-projects {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}

.profile-page .profile-extra .row .col1 .pending-projects .pending-projects-list {
  margin-top: 15px;
  width: 100%;
  height: 250px;
  overflow-y: auto;
}

.profile-page .profile-extra .row .col1 .pending-projects .pending-projects-list::-webkit-scrollbar {
  width: 1em;
}

.profile-page .profile-extra .row .col1 .pending-projects .pending-projects-list::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
}

.profile-page .profile-extra .row .col1 .pending-projects .pending-projects-list::-webkit-scrollbar-thumb {
  background-color: var(--ocean-blue);
  outline: 1px solid slategrey;
  border-radius: 5px;
}

.profile-page .profile-extra .row .col1 .pending-projects .pending-projects-list ul {
  list-style: none;
  font-size: 18px;
  font-style: italic;
}

.profile-page .profile-extra .row .col1 .pending-projects .pending-projects-list ul li:not(:first-child) {
  margin-top: 10px;
}

.profile-page .profile-extra .row .col1 .progress-overview .progress-graph figure svg {
  margin: 0 auto;
}

.profile-page .profile-extra .row .col1 .progress-overview .progress-graph figure figcaption {
  /**/
}

.profile-page .profile-extra .row .col1 .progress-overview .progress-graph figure svg .bar {
  fill: var(--sea-blue);
  height: 21px;
  transition: fill .3s ease;
  cursor: pointer;
  font-family: 'Roboto', sans-serif;
}

.profile-page .profile-extra .row .col1 .progress-overview .progress-graph figure svg .bar text {
  color: var(--dark-gray);
}

.profile-page .profile-extra .row .col1 .progress-overview .progress-graph figure svg .bar:hover,
.profile-page .profile-extra .row .col1 .progress-overview .progress-graph figure svg .bar:focus {
  fill: var(--light-blue);
}

.profile-page .profile-extra .row .col1 .progress-overview .progress-graph figure svg .bar text:hover,
.profile-page .profile-extra .row .col1 .progress-overview .progress-graph figure svg .bar text:focus {
  fill: var(--red-lightish);
}

.profile-footer {
  text-align: center;
  color: var(--dark-gray);
  font-size: 32px;
  font-family: "Roboto", sans-serif;
  font-weight: 900;
}

@media (max-width: 480px) {
  .login-form {
    width: 100%;
    display: block;
    align-content: center;
  }

  .login-form form .input-container .input-icon img {
    width: 20px;
    height: 20px;
    display: none;
  }

  .login-form form input[type='text'],
  .login-form form input[type='password'] {
    width: 80%;
    height: 50px;
  }

  .login-form form input[type='submit'] {
    width: 50%;
    height: 65px;
    font-size: 28px;
  }

  .profile-page {
    display: block;
  }

  .profile-page .profile-header {
    display: block;
    text-align: center;
    width: 100%;
  }

  .profile-page .profile-header .column-1 {
    width: 100%;
    min-width: 100%;
  }

  .profile-page .profile-header .column-1 .row h1 {
    text-align: center;
  }

  .profile-page .profile-body {
    width: 100%;
    margin-left: 0;
    margin-top: 10px;
  }

  .profile-page .profile-body .row {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .profile-page .profile-body .row .col {
    width: 100%;
  }

  .profile-page .profile-body .row .col .overview .recent-projects ul {
    width: 100%;
    overflow-y: scroll;
  }

  .profile-page .profile-extra {
    width: 100%;
    margin-left: 0;
    margin-top: 10px;
  }
}