@import url('https://fonts.googleapis.com/css2?family=Ga+Maamli&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

::-moz-selection {
    background-color: var(--ocean-blue);
    color: var(--white);
}

::selection {
    background-color: var(--ocean-blue);
    color: var(--white);
}

:root {
    /* CSS HEX */
    --mantis: #79b473;
    --cambridge-blue: #70a37f;
    --lapis-lazuli: #41658a;
    --delft-blue: #414073;
    --english-violet: #4c3957;
    --english-lighter: #caa4e0;
    --light-gray: #868c96;
    --red-lightish: #ff3d61;
    --red-lighter: #ff9cae;
    --white: #fff;
    /*NEW COLLORS*/
    --dark-gray: #474747;
    --lighter-gray: #dbdbdb;
    --lighter-blue: #C2E5F2;
    --ocean-blue: #04B2D9;
    --light-blue: #88DFF2;
    --beach-blue: #04C4D9;
    --sea-blue: #04D9D9;
}